import React, { Component } from "react";
import * as routes from '../../constants/routes';
import Axios from 'axios';

let message_template_detail = {
    message_id :'',
    subject : '',
    template :'',  
    send_options: ''  , //SMS,Email,Voice,Both
    message_type :'', //payment reminder, payment notification, non payment reminder, birthday message,
    //promotional message

};

let sent_report_detail = {
    message_id :'',
    sent_id : '',
    sent_date : '',
    sent_time : '',
    status : '',
    cell:'',
    email:'',
    names:''
};

let message_response_detail ={
    message_id : '',
    sent_id:'',
    response:'',
    response_date : '',
    response_time : '',
    cell:'',
    email:'',
    names:''
};

let message_settings_detail = {
    
    send_payment_notification : false,
    send_non_payment_notification : false,
    send_payment_reminder : false,
    send_birthday_messages : false,
    send_promotional_messages : false,
    send_sms_message : false,
    send_email_message : false,
    send_voice_message : false,
    send_smsemail_message : false
};


class MessageSettings extends Component{
    constructor(){
        super();
        this.state = {
            message_settings: message_settings_detail,
            form_response : ''
        };
        this.onChangeHandler = this.onChangeHandler.bind(this);
    };

    onChangeHandler(e){
        let message_settings = Object.assign({},this.state.message_settings);
        console.log(e.target.value);
        switch(e.target.name){
            case 'send_payment_notification': message_settings.send_payment_notification = !message_settings.send_payment_notification; break;                                    
            case 'send_non_payment_notification': message_settings.send_non_payment_notification = !message_settings.send_non_payment_notification; break;
            case 'send_payment_reminder': message_settings.send_payment_reminder = !message_settings.send_payment_reminder; break;
            case 'send_birthday_messages': message_settings.send_birthday_messages = !message_settings.send_birthday_messages;break;
            case 'send_promotional_messages': message_settings.send_promotional_messages = !message_settings.send_promotional_messages;break;
            case 'send_sms_message': message_settings.send_sms_message = !message_settings.send_sms_message;break;
            case 'send_email_message': message_settings.send_email_message = !message_settings.send_email_message;break;
            case 'send_voice_message': message_settings.send_voice_message = !message_settings.send_voice_message;break;
            case 'send_smsemail_message': message_settings.send_smsemail_message = !message_settings.send_smsemail_message;break;
            default:break;
        };

        this.setState({
            message_settings:message_settings
        });

        console.log(this.state.message_settings);
    }


    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}><strong> <i className={'fa fa-cogs'}> </i> Message Settings</strong></h3>
                    <div className={'row pull-right'}>
                        <button type={'button'} className='btn btn-box-tool btn-outline-danger' onClick={e => {
                            return (
                                this.props.onClose(e)
                            )
                        }}><strong><i className='fa fa-close'> </i> Close </strong></button>
                    </div>
                </div>

                <div className={'box box-footer'}>
                        <div className={'box box-header'}>
                        <h3 className={'box-title'}> <i className={'fa fa-newspaper-o'}> </i> Notification Settings</h3>
                        </div>
                    <ul className={'list-group'}>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_payment_notification' value={this.state.message_settings.send_payment_notification} onChange={e => this.onChangeHandler(e)} /> Payment Notifications  </li>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_non_payment_notification' value={this.state.message_settings.send_non_payment_notification} onChange={e => this.onChangeHandler(e)} /> Send Notifications on Defaults </li>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_payment_reminder' value={this.state.message_settings.send_payment_reminder} onChange={e => this.onChangeHandler(e)} /> Send Payments Reminders </li>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_birthday_messages' value={this.state.message_settings.send_birthday_messages} onChange={e => this.onChangeHandler(e)} /> Send Birthday Messages </li>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_promotional_messages' value={this.state.message_settings.send_promotional_messages} onChange={e => this.onChangeHandler(e)} /> Send Promotional Messages </li>
                    </ul>

                </div>

            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                        <h3 className={'box-title'}> <strong> <i className={'fa fa-envelope-o'}> </i>  Sending Options</strong></h3>
                </div>
                    <ul className={'list-group'}>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_sms_message' value={this.state.message_settings.send_sms_message} onChange={e => this.onChangeHandler(e)} /> Send SMS Messages </li>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_email_message' value={this.state.message_settings.send_email_message} onChange={e => this.onChangeHandler(e)} /> Send Email Messages </li>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_voice_message' value={this.state.message_settings.send_voice_message} onChange={e => this.onChangeHandler(e)} /> Send Voice Message </li>
                        <li className={'list-group-item'}> <input type={'checkbox'} name='send_smsemail_message' value={this.state.message_settings.send_smsemail_message} onChange={e => this.onChangeHandler(e)} /> Send SMS &amp; Email Message </li>
                    </ul>
            </div>

            <div className={'box box-footer'}>
                <button className={'btn btn-app btn-block btn-outline-danger'}><strong> <i className={'fa fa-save'}> </i> Save &amp; Activate Settings</strong></button>
            </div>
        </div>
        )
    }
}


class CreateMessage extends Component{
    constructor(props){
        super(props);
        this.state = {
            message_template : message_template_detail,
            form_response : ''
        };

        this.onChangeHandler = this.onChangeHandler.bind(this);
        this.onShow = this.onShow.bind(this);
    };

    onShow(e){

    }

    onChangeHandler(e){
        let message_template = Object.assign({},this.state.message_template);
        switch(e.target.name){
            case 'subject' : message_template.subject = e.target.value;break;
            case 'template' : message_template.template = e.target.value;break;
            case 'message_type' : message_template.message_type = e.target.value;break;
            case 'send_options' : message_template.send_options = e.target.value;break;
            default:break;
        };

        this.setState({
            message_template:message_template
        });

    }

    render(){
        return(
            <div className='box box-footer'>
                <div className='box box-header'>
                    <h3 className='box-title'> <strong> <i className='fa fa-send'> </i> Create Message </strong></h3>
                    
                    <div className='row pull-right'>
                        <button type={'button'} className='btn btn-box-tool btn-outline-dark' onClick={e => this.onShow(e)}><strong><i className='fa fa-file-archive-o'> </i> Template Language</strong></button>
                        <button type={'button'} className='btn btn-box-tool btn-outline-danger' onClick={e => {
                            return (
                                this.props.onClose(e)
                            )
                        }}><strong><i className='fa fa-close'> </i> Close </strong></button>
                    </div>

                </div>


                <form className={'form-horizontal'}>
                    <div className={'form-group'}>
                        <label>Message Type</label>
                        <div className={'form-group'}>
                            <select name={'message_type'} className={'form-control'} value={this.state.message_template.message_type} onChange={e => this.onChangeHandler(e)}>
                                <option value={'payment-reminder'}> Payment Reminder</option>
                                <option value={'payment-notification'}> Payment Notification</option>
                                <option value={'non-payment-reminder'}> Non Payment Reminder</option>
                                <option value={'birthday-message'}> Birthday Message</option>
                                <option value={'promotional-message'}> Promotional Message</option>
                            </select>
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label>Subject</label>
                        <div className={'input-group'}>
                            <input type={'text'} className={'form-control'} name={'subject'} value={this.state.message_template.subject} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label>Message Template</label>
                        <div className={'input-group'}>
                            <textarea className={'form-control'} name={'template'} value={this.state.message_template.template} onChange={e => this.onChangeHandler(e)}></textarea>
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label>Sendind Options</label>
                        <div className={'input-group'}>
                            <select name={'send_options'} className={'form-control'} value={this.state.message_template.send_options} onChange={e => this.onChangeHandler(e)} >
                                <option value={'sms'}>SMS</option>
                                <option value={'voice'}>Voice</option>
                                <option value={'email'}>Email</option>
                                <option value={'smsemail'}>SMS &amp; Email</option>
                            </select>
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <div className={'input-group'}>
                            <button type={'button'} className={'btn btn-app btn-block btn-outline-dark'}><strong><i className={'fa fa-save'}> </i> Save Template</strong></button>
                        </div>
                    </div>
                
                </form>


            </div>
        )
    }
}


