class Messages extends Component{
    constructor(props){
        super(props);
        this.state = {
            messages_list : [],
            show_message_list : false,
            show_sent_reports : false,
            show_message_responses : false,
        };
        this.onShow = this.onShow.bind(this);
        this.loadMessagesList = this.loadMessagesList.bind(this);
    };

    loadMessagesList(e){

    };

    componentWillMount(e){
        this.loadMessagesList(e); 
    }

    onShow(e){

        switch(e.target.name){
            case 'message-list':
                this.setState({
                    show_message_list: true,
                    show_sent_reports: false,
                    show_message_responses: false,
                });break;
            case 'message-responses':
                this.setState({
                    show_message_list: false,
                    show_sent_reports: false,
                    show_message_responses: true,
                }); break;
            case 'sent-reports':
                this.setState({
                    show_message_list: false,
                    show_sent_reports: true,
                    show_message_responses: false,
                }); break;

            default: break;
        }

    }


    render(){
        return(
            <div className='box box-footer'>
                <div className='box box-header'>
                    <h3 className='box-title'><strong> <i className='fa fa-th-list'> </i> Messages </strong></h3>
                    <div className='row pull-right'>
                        <button type='button' className='btn btn-box-tool btn-outline-dark' name={'message-list'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-envelope-square'> </i> Messages </strong></button>
                        <button type='button' className='btn btn-box-tool btn-outline-dark' name={'message-responses'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-mail-reply'> </i> Message Responses </strong></button>
                        <button type='button' className='btn btn-box-tool btn-outline-dark' name={'sent-reports'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-sticky-note-o'> </i> Sent Reports </strong></button>

                        <button type='button' className='btn btn-box-tool btn-outline-danger' onClick={e => {
                            return (
                                this.props.onClose(e)
                            )
                        }}><strong><i className='fa fa-close'> </i> Close </strong></button>

                    </div>

                </div>

                {
                    (this.state.show_message_list)?
                        <MessagesList />
                    :''
                }
                {
                    (this.state.show_message_responses)?
                        <MessageResponse />
                    :''
                }
                {
                    (this.state.show_sent_reports)?
                        <SentReports />
                    :''
                }
            </div>
        )
    }
}


class Messaging extends Component{
    constructor(){
        super();
        this.state = {
            messaging : "",
            show_messages_list : false,
            show_create_message : false,
            show_message_settings : false
        };

        this.onShow = this.onShow.bind(this);
        this.onCloseHandler = this.onCloseHandler.bind(this);
    };

    onCloseHandler(e){
        this.setState({
            show_messages_list: false,
            show_create_message: false,
            show_message_settings: false
        });
    }

    onShow(e){

        switch(e.target.name){
            case 'messages' : 
                this.setState({
                    show_messages_list: true,
                    show_create_message: false,
                    show_message_settings: false
                });break;

            case 'create-message' :
                this.setState({
                    show_messages_list: false,
                    show_create_message: true,
                    show_message_settings: false
                });break;

            case 'message-settings':
                this.setState({
                    show_messages_list: false,
                    show_create_message: false,
                    show_message_settings: true
                });break;
            default:break;
        }
    }

    render(){
        return(
            <div className='box box-footer'>
                <div className='box box-header'>
                    <h3 className='box-title'><strong> <i className='fa fa-envelope-o'> </i>Messaging</strong></h3>

                    <div className='row pull-right'>
                        
                        <button className='btn btn-box-tool btn-outline-dark' name='messages' onClick={e => this.onShow(e)}><strong> <i className='fa fa-th-list'> </i> Messages</strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name='create-message' onClick={e => this.onShow(e)}><strong> <i className='fa fa-send-o'> </i> Create Message</strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name='message-settings' onClick={e => this.onShow(e)}><strong> <i className='fa fa-cogs'> </i> Settings</strong></button>
                    </div>


                </div>

                {
                    !(this.state.show_messages_list || this.state.show_create_message || this.state.show_message_settings) ?
                        <div className='box box-footer'>
                            <div className='box box-header'>
                                <h3 className='box-title'><strong> <i className='fa fa-envelope'></i> Messaging Module</strong></h3>
                            </div>

                            <span>This module allows you to centrally manage communications with your clients, 
                                the module is fully intergrated with your covers module allowing you to automatically 
                                send payment notifications, payment reminders and also send in promotional materials 
                                to your clients without having to spend more time that it took to originally 
                                capture the client data when creating a cover/policy.
                            </span>

                            <div className={'box box-footer'}>
                                <div className={'box box-header'}>
                                    <h3 className={'box-title'}><strong> Module Features</strong></h3>
                                </div>

                                <ul className={'list-group'}>
                                    <li className={'list-group-item'}> Module can automatically send Payment Notifications </li>
                                    <li className={'list-group-item'}> Module can automatically send Payment Reminders </li>
                                    <li className={'list-group-item'}> Module can automatically send Promotional Messages </li>
                                    <li className={'list-group-item'}> Module can send Both SMS,Email &amp; Voice Messages </li>
                                </ul>

                            </div>
                        </div>
                    :""
                }


                {
                    (this.state.show_messages_list) ?

                        <Messages 
                            onClose={this.onCloseHandler}
                        />
                    :""
                }

                {
                    (this.state.show_create_message)?
                        <CreateMessage 
                            onClose={this.onCloseHandler}
                        />
                    :""
                }
                {
                    (this.state.show_message_settings) ?
                        <MessageSettings 
                            onClose={this.onCloseHandler}
                        />
                    :''
                }
            </div>
        )
    }
}

export default Messaging;