import React, { Component } from "react";
import * as routes from '../../../constants/routes';
import Axios from 'axios';
import * as Address from '../Addresses';
import * as Utilities from '../../../constants/utilities';
import * as leads_routes from '../../../constants/leads-routes';

let leads_details = {
    lead_id : "",
    full_names : "",
    surname : "",
    cell : "",
    email : "",
    id_number : "",
    notes :""
}

let leads_extra_details = {
    lead_id : "",
    date_of_birth : "",
    residential_address: Address.physical_address_details,

}

class CreateLeads extends Component{
    constructor(props){
        super(props);
        this.state = {
            lead : leads_details,
            form_response : ""
        };

        this.onChangeHandler = this.onChangeHandler.bind(this);
        this.onSaveHandler = this.onSaveHandler.bind(this);
    };

    onChangeHandler(e){
        let lead = Object.assign({},this.state.lead);
        switch(e.target.name){
            case 'full_names' : lead.full_names = e.target.value;break;
            case 'surname' : lead.surname = e.target.value;break;
            case 'cell' : lead.cell = e.target.value;break;
            case 'email' : lead.email = e.target.value;break;
            case 'id_number' : lead.id_number = e.target.value;break;
            case 'notes' : lead.notes = e.target.value;break;
            default:break;
        }

        this.setState({
            lead:lead
        });
    }

    onSaveHandler(e){
        let isError = false;
        let errorMessage = '';
        let self = this;

        if (Utilities.isEmpty(this.state.lead.full_names)){
            isError = true;
            errorMessage = 'Full Names cannot be empty';
        }

        if (Utilities.isEmpty(this.state.lead.surname)){
            isError = true;
            errorMessage = 'Surname cannot be empty';
        }

        if (!Utilities.isCell(this.state.lead.cell)){
            isError = true;
            errorMessage = 'Invalid Cell Number';
        }

        if (!isError){
            Axios.post(leads_routes.save_leads_url,'&data=' + JSON.stringify(this.state.lead)).then(
                function(response){
                    if (response.status === 200){
                        return response.data
                    }else{
                        throw new Error('Error saving lead');
                    }
                }
            ).then(function(json_data){
                let message = json_data.message;
                self.setState({
                    form_response : message
                });
            }).catch(function(err){
                let message = err.message;
                self.setState({
                    form_response : message
                });
            });
        }else{
            self.setState({
                form_response : errorMessage
            });
        }    
    }

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <i className='fa fa-spinner'> </i> Create Lead </strong></h3>
                    <div className='row pull-right'>
                        <button className='btn btn-box-tool btn-outline-dark' onClick={e => {
                            return (
                                this.props.onClose(e)
                            )
                        }}><strong><i className='fa fa-close'> </i> Close </strong></button>
                    </div>

                </div>

                <form className='form-horizontal col-md-6'>
                    <div className='form-group'>
                        <label>Full Names</label>
                        <div className='input-group'>
                            <input type='text' className='form-control' name='full_names' value={this.state.lead.full_names} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>
                    <div className='form-group'>
                        <label>Surname</label>
                        <div className='input-group'>
                            <input type='text' className='form-control' name='surname' value={this.state.lead.surname} onChange={e => this.onChangeHandler(e)}/>
                        </div>
                    </div>
                    <div className='form-group'>
                        <label>Cell</label>
                        <div className='input-group'>
                            <input type='tel' className='form-control' name='cell' value={this.state.lead.cell} onChange={e => this.onChangeHandler(e)}/>
                        </div>
                    </div>

                    <div className='form-group'>
                        <label>Email</label>
                        <div className='input-group'>
                            <input type='email' className='form-control' name='email' value={this.state.lead.email} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className='form-group'>
                        <label>ID Number</label>
                        <div className='input-group'>
                            <input type='text' className='form-control' name='id_number' value={this.state.lead.id_number} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className='form-group'>
                        <label>Notes</label>
                        <div className='input-group'>
                            <textarea className='form-control' name='notes' value={this.state.lead.notes} onChange={e => this.onChangeHandler(e)}></textarea>
                        </div>
                    </div>

                    <div className='input-group'>
                        <button type='button' className='btn btn-success btn-block' onClick={e => this.onSaveHandler(e)}> <strong> <i className='fa fa-save'> </i> Save Lead</strong></button>
                    </div>

                    <div className='form-group'>
                        {
                            (this.state.form_response !== undefined) ?
                                <span>{this.state.form_response}</span>
                            :""
                        }
                    </div>
                </form>
            </div>
        )
    }
}


class LeadItem extends Component{
    constructor(props){
        super(props);
        this.state = {
            lead : this.props.lead
        };
    };

    render(){
        return(
            <tr>
                <td>{this.state.lead.full_names}</td>
                <td>{this.state.lead.surname}</td>
                <td>{this.state.lead.cell}</td>
                <td>{this.state.lead.email}</td>
                <td>{this.state.lead.date}</td>
            </tr>
        )
    }
}

class LeadsList extends Component{
    constructor(props){
        super(props);
        this.state = {
            leads_list : [],
            form_response : ""
        };
    };

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}><strong> <i className='fa fa-server'> </i> Leads List</strong></h3>

                    <div className='row pull-right'>
                        <button className='btn btn-box-tool btn-outline-dark' onClick={e => {
                            return(
                                this.props.onClose(e)
                            )
                        }}><strong><i className='fa fa-close'> </i> Close </strong></button>
                    </div>
                </div>        

                <table className='table table-responsive table-borderless'>
                    <thead>
                        <tr>
                            <td>Names</td>
                            <td>Surname</td>
                            <td>Cell</td>
                            <td>Email</td>
                            <td>Date</td>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.leads_list.map((lead,index) => {
                                return(
                                    <LeadItem
                                        lead = {lead}
                                        key={index}
                                    />
                                )
                            })
                        }
                    </tbody>
                    <tfoot>
                        <tr>
                            <td>Names</td>
                            <td>Surname</td>
                            <td>Cell</td>
                            <td>Email</td>
                            <td>Date</td>
                        </tr>
                    </tfoot>
                </table>

            </div>
        )
    }
}

class Leads extends Component{
    constructor(props){
        super(props);
        this.state = {
            leads : leads_details,
            leads_extra : leads_extra_details,
            form_response : '',

            show_leads_list : false,
            show_create_leads : false,

        };

        this.onShow = this.onShow.bind(this);
        this.onCloseHandler = this.onCloseHandler.bind(this);
    };

    onCloseHandler(e){
        this.setState({
            show_leads_list: false,
            show_create_leads: false,
        });
    }
    

    onShow(e){
        switch(e.target.name){
            case 'leads-list':
            this.setState({
                show_leads_list: true,
                show_create_leads: false,

            });break;

            case 'create-lead':
            this.setState({
                show_leads_list: false,
                show_create_leads: true,
            })
            default:break;
        }
    }

    render(){
        return(
            <div className='box box-footer'>
                <div className='box box-header'>
                    <h3 className='box-title'> <strong> <i className={'fa fa-th-list'}> </i> Leads</strong> </h3>

                    <div className={'row pull-right'}>
                        <button className='btn btn-box-tool btn-outline-dark' name={'leads-list'} onClick={e => this.onShow(e)}><strong> <i className={'fa fa-server'}> </i> Leads </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'create-lead'} onClick={e => this.onShow(e)}><strong> <i className={'fa fa-server'}> </i> Create Leads </strong></button>                        
                    </div>
                </div>
                {
                    !(this.state.show_create_leads || this.state.show_leads_list) ?
                        <div className='box box-footer'>
                            <div className='box box-header'>
                                <h3 className='box-title'><strong> Leads Module</strong></h3>
                            </div>

                            <span>
                                This module allows you to run your lead gathering campaigns more 
                                efficiently and also successfully by centralizing lead information
                                capturing and responses.

                                the module insure's that:

                                <ul className='list-group'>
                                    <li className='list-group-item'> <em>Every lead is unique</em></li>
                                    <li className='list-group-item'> <em>Follow up is done timely and with the right information</em></li>
                                    <li className='list-group-item'> <em>Feedback from lead is captured and also used</em></li>
                                    <li className='list-group-item'> <em>We maximise lead conversion by personalizing potential clients experiences with our marketing department</em></li>

                                </ul>
                            </span>
                        </div>
                        : ""
                }
                
                {
                    (this.state.show_leads_list) ?
                        <LeadsList 
                            onClose = {this.onCloseHandler}
                        />
                    :""
                }
                {
                    (this.state.show_create_leads) ?
                       <CreateLeads 
                            onClose = {this.onCloseHandler}
                       />
                    :""
                }


            </div>
        )
    }
}


export default Leads;