import React, { Component } from "react";
import * as routes from '../../../constants/routes';
import Axios from 'axios';
import * as Addresses from '../Addresses/';
import * as Contacts from '../ContactDetails';
import * as banking_routes from '../../../constants/banking-routes';
import * as address_routes from '../../../constants/addresses-routes';
import * as contacts_routes from '../../../constants/contacts-routes';
import * as policy_routes from '../../../constants/policy-routes';
import * as Utilities from '../../../constants/utilities';

let policy_holder_details = {
    policy_number : "",
    title : "",
    surname : "",
    full_names : "",
    id_number : "",
    date_of_birth : "",
    nationality : "",

};

let banking_details = { 
    policy_number : "",
    account_holder : "",
    account_number : "",
    bank : "",
    branch_code : "",
    branch_name : "",

};


let spouse_details = {
    policy_number : "",
    full_names : "",
    surname : "",
    id_number : "",
    date_of_birth : "",
    relationship : ""
    
};

let child_details = {
    policy_number : "",
    full_names : "",
    surname : "",
    id_number : "",
    date_of_birth : "",
    relationship : ""

};

let extended_details = {
    policy_number : "",
    full_names : "",
    surname : "",
    id_number : "",
    date_of_birth : "",
    relationship : ""

};

let beneficiary_details = {
    policy_number : "",
    full_names : "",
    surname : "",
    id_number : "",
    date_of_birth : "",
    relationship : ""
};

class BankingDetails extends Component{
    constructor(props){
        super(props);
        this.state = {
            banking_detail: banking_details ,
            form_response : ""
        };

        this.onChangeHandler = this.onChangeHandler.bind(this);
        this.onSaveHandler = this.onSaveHandler.bind(this);
    };

    onChangeHandler(e){
        let banking_details = Object.assign({},this.state.banking_detail);

        switch(e.target.name){
            case 'account_holder' : banking_details.account_holder = e.target.value;break;
            case 'account_number' : banking_details.account_number = e.target.value;break;
            case 'bank' : banking_details.bank = e.target.value;break;
            case 'branch_code' : banking_details.branch_code = e.target.value;break;
            case 'branch_name' : banking_details.branch_name = e.target.value;break;
            default:break;
        };

        this.setState({
            banking_detail : banking_details
        });

    }

    onSaveHandler(e){
        let self = this;
        let isError = false;
        let errorMessage = '';
        if (Utilities.isEmpty(this.state.banking_detail.account_holder)){
            isError = true;
            errorMessage = 'Account Holder cannot be empty';
            
        }

        if (Utilities.isEmpty(this.state.banking_detail.account_number)){
            isError = true;
            errorMessage = 'Account Number cannot be empty';
        }

        if (Utilities.isEmpty(this.state.banking_detail.bank)){
            isError = true;
            errorMessage = 'Bank name cannot be empty';
        }

        if (Utilities.isEmpty(this.state.banking_detail.branch_code)){
            isError = true;
            errorMessage = 'Branch Code cannot be empty';
        }

        if (!isError){
            let banking_detail = Object.assign({},this.state.banking_detail);
            
            Axios.post(banking_routes.save_banking_details_url,'&data='+JSON.stringify(banking_detail)).then(
                function(response){
                    if (response.status === 200){
                        return response.data
                    }else{
                        throw new Error('Error saving banking details');
                    }
                }
            ).then(function(json_data){
                let message = json_data.message;
                self.setState({
                    form_response : message
                });
            }).catch(function(err){
                let message = err.message;
                self.setState({
                    form_response : message
                });
            });
        }else{
            self.setState({
                form_response : errorMessage
            })
        }

    }

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <i className={'fa fa-bank'}> </i> Banking Details </strong></h3>
                </div>

                <form className={'form-horizontal'}>
                    <div className={'form-group'}>
                        <label>Account Holder</label>
                        <div className={'input-group'}>
                            <input type={'text'} className={'form-control'} name={'account_holder'} value={this.state.banking_detail.account_holder} onChange={ e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label> Account Number</label>
                        <div className={'input-group'}>
                            <input type={'text'} className={'form-control'} name={'account_number'} value={this.state.banking_detail.account_number} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label>Bank</label>
                        <div className={'input-group'}>
                            <select name={'bank'} className={'form-control'} value={this.state.banking_detail.bank} onChange={e => this.onChangeHandler(e)}>

                                <option value={'fnb'}>FNB</option>
                                <option value={'standard'}>Standard</option>
                                <option value={'absa'}>ABSA</option>
                                <option value={'nedbank'}>Nedbank</option>
                                <option value={'capitec'}>Capitec</option>
                                <option value={'postbank'}>Post Bank</option>
                            </select>
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label>Branch Code</label>
                        <div className={'input-group'}>
                            <input type={'text'} name={'branch_code'} className={'form-control'} value={this.state.banking_detail.branch_code} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label>Branch Name</label>
                        <div className={'input-group'}>
                            <input type={'text'} name={'branch_name'} className={'form-control'} value={this.state.banking_detail.branch_name} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <button type={'button'} className={'btn btn-success btn-block'} onClick={e => this.onSaveHandler(e)}> <strong> <i className={'fa fa-save'}> </i> Save Banking Details </strong> </button>
                    </div>
                    <div className={'form-group'}>
                        {
                            (this.state.form_response !== undefined)?
                                <span>{this.state.form_response}</span>
                            :""
                        }
                    </div>
                </form>
            </div>
        )
    }
}

class Spouse extends Component{
    constructor(props){
        super(props);
        this.state = {
            spouse_list : [],
            spouse_detail : {...spouse_details},
            form_response : ""
        };

        this.onChangeHandler = this.onChangeHandler.bind(this);
        this.onSaveSpouse = this.onSaveSpouse.bind(this);
        this.loadSpouse = this.loadSpouse.bind(this);
    };


    onSaveSpouse(e){
        let isError = false;
        let errorMessage = '';
        let self = this;

        if (Utilities.isEmpty(this.state.spouse_detail.full_names)){
            isError = true;
            errorMessage = 'Full Names cannot be empty';
        };

        if (Utilities.isEmpty(this.state.spouse_detail.surname)){
            isError = true;
            errorMessage = 'Surname cannot be empty';
        };

        if (Utilities.isIDNumber(this.state.spouse_detail.id_number)){
            isError = true;
            errorMessage = 'ID Number is not valid';
        };

        if (Utilities.isEmpty(this.state.spouse_detail.date_of_birth)){
            isError = true;
            errorMessage = 'Date of Birth cannot be empty';
        };

        if (Utilities.isEmpty(this.state.spouse_detail.relationship)){
            isError = true;
            errorMessage = 'Please select relationship';
        };

        if(!isError){

            let spouse_detail = Object.assign({},this.state.spouse_detail);

            Axios.post(policy_routes.save_spouse_url,'&data=' + JSON.stringify(spouse_detail)).then(
                function(response){
                    if (response.status === 200){
                        return response.data;
                    }else{
                        throw new Error('Error saving spouse details');
                    }
                }).then(function(json_data){
                    let message = json_data.message;
                    self.setState({
                        form_response : message
                    });
                }).catch(function(err){
                    let message = err.message;
                    self.setState({
                        form_response : message
                    });
                });

        }else{
            self.setState({
                form_response : errorMessage
            })
        }
    };

    onChangeHandler(e){
        let spouse_detail = Object.assign({}, this.state.spouse_detail);
        
        switch (e.target.name) {
            case 'full_names' : spouse_detail.full_names = e.target.value;break;
            case 'surname' : spouse_detail.surname = e.target.value;break;
            case 'id_number' : spouse_detail.id_number = e.target.value;break;
            case 'date_of_birth' : spouse_detail.date_of_birth = e.target.value;break;
            case 'relationship' : spouse_detail.relationship = e.target.value;break;
            default:break;
        }

        this.setState({
            spouse_detail : spouse_detail
        });
    }

    loadSpouse(e){
        let self = this;
        Axios.get(routes.load_spouse_url).then(function(response){
            if (response.status === 200){
                return response.data
            }else{
                throw new Error("Error loading spouse data");
            }
        }).then(function(json_data){
            self.setState({
                spouse_list: json_data
            });
        }).catch(function(err){
            let message = err.message;
            self.setState({
                form_response : message
            });
        });
    }

    componentWillMount(e){
        this.loadSpouse(e);
    }


    render(){
        
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <i className={'fa fa-user'}> </i> Spouse Details</strong></h3>
                </div>
                <div className={'row'}>
                    <div className={'col-md-6'}>
                        <form className={'form-horizontal'}>
                            <div className={'form-group'}>
                                <label>Names</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'full_names'} value={this.state.spouse_detail.full_names} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>Surname</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'surname'} value={this.state.spouse_detail.surname} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>ID Number</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'id_number'} value={this.state.spouse_detail.id_number} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>Date Of Birth</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'date_of_birth'} value={this.state.spouse_detail.date_of_birth} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label> Relationship </label>
                                <div className={'input-group'}>
                                    <select className={'form-control'} name={'relationship'} value={this.state.spouse_detail.relationship} onChange={e => this.onChangeHandler(e)} >
                                        <option value={'wife'}>Wife</option>
                                        <option value={'husband'}>Husband</option>
                                    </select>
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <button type={'button'} className={'btn btn-success btn-block'} name={'save_spouse'} onClick={e => this.onSaveSpouse(e)}><strong> <i className={'fa fa-save'}> </i> Save Spouse </strong></button>
                            </div>
                            <div className={'form-group'}>
                                {
                                    (this.state.form_response !== undefined) ?
                                        <span>{this.state.form_response}</span>
                                    :""
                                }
                            </div>
                        </form>
                    </div>
                    <div className={'col-md-6'}>
                        <SubDisplay
                            screen_title = {'Spouse Details'}
                            screen_data={this.state.spouse_list}
                        />
                    </div>
                </div>
            </div>
        )
        
    }
}

class Child extends Component{
    constructor(props){
        super(props);
        this.state = {
            child_list : [],
            child_detail : {...child_details},
            form_response : ""
        };

        this.onChangeHandler = this.onChangeHandler.bind(this);
        this.onSaveChild = this.onSaveChild.bind(this);
        this.loadChildrens = this.loadChildrens.bind(this);
    };

    onChangeHandler(e){
        let child_detail = Object.assign({}, this.state.child_detail);

        switch (e.target.name) {
            case 'full_names' : child_detail.full_names = e.target.value;break;
            case 'surname' : child_detail.surname = e.target.value;break;
            case 'id_number' : child_detail.id_number = e.target.value;break;
            case 'date_of_birth' : child_detail.date_of_birth = e.target.value;break;
            case 'relationship' : child_detail.relationship = e.target.value;break;
            default:break;
        }

        this.setState({
            child_detail : child_detail
        });

    }

    onSaveChild(e){
        let isError = false;
        let errorMessage = '';
        let self = this;

        if (Utilities.isEmpty(this.state.child_detail.full_names)){
            errorMessage = 'Full Names cannot be empty';
            isError = true;
        }
        if (Utilities.isEmpty(this.state.child_detail.surname)){
            errorMessage = 'Surname cannot be empty';
            isError = true;
        }

        if (!Utilities.isIDNumber(this.state.child_detail.id_number)){
            errorMessage = 'Invalid ID Number';
            isError = true;
        }

        if (Utilities.isEmpty(this.state.child_detail.date_of_birth)){
            errorMessage = 'Date of Birth cannot be empty';
            isError = true;
        }

        if (Utilities.isEmpty(this.state.child_detail.relationship)){
            errorMessage = 'Please select a Relationship';
            isError = true;
        }


        if (!isError){


        Axios.post(policy_routes.save_child_url,'&data=' +JSON.stringify(this.state.child_detail)).then(
            function(response){
                if (response.status === 200){
                    return response.data;
                }else{
                    throw new Error('Error saving child');
                }
            }).then(function(json_data){
                let message = json_data.message;
                self.setState({
                    form_response : message
                });
            }).catch(function(err){
                let message = err.message;
                self.setState({
                    form_response : message
                });
            })

        }else{
            self.setState({
                form_response : errorMessage
            })
        }

    }

    loadChildrens(e){
        let self = this;
        Axios.get(routes.load_children_url).then(function(response){
            if (response.status === 200){
                return response.data;
            }else{
                throw new Error('Error loading children data');
            }
        }).then(function(json_data){
            self.setState({
                child_list : json_data
            });
        }).catch(function(err){
            let message = err.message;
            self.setState({
                form_response : message
            });
        });

    }

    componentWillMount(e){
        this.loadChildrens(e);
    }

    render(){
        return(
            <div className={'box box-body'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}><strong> <i className={'fa fa-users'}> </i> Children </strong></h3>
                </div>


                <div className={'row'}>
                    <div className={'col-md-6'}>
                        <form className={'form-horizontal'}>
                            <div className={'form-group'}>
                                <label>Names </label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'full_names'} value={this.state.child_detail.full_names} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form--group'}>
                                <label>Surname</label>
                                <div className={'form-group'}>
                                    <input type={'text'} className={'form-control'} name={'surname'} value={this.state.child_detail.surname} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>ID Number</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'id_number'} value={this.state.child_detail.id_number} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>
                            
                            <div className={'form-group'}>
                                <label>Date Of Birth</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'date_of_birth'} value={this.state.child_detail.date_of_birth} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>Relationship</label>
                                <div className={'input-group'}>

                                    <select className={'form-control'} name={'relationship'} value={this.state.child_detail.relationship} onChange={e => this.onChangeHandler(e)} >
                                        <option value={'daughter'}>Daughter</option>
                                        <option value={'son'}>Son</option>
                                    </select>
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <div className={'input-group'}>
                                    <button type={'text'} name={'save_child'} className={'btn btn-success btn-block'} onClick={e => this.onSaveChild(e)}> <strong> <i className={'fa fa-save'}> </i> Save Child </strong> </button>
                                </div>
                            </div>
                            <div className={'form-group'}>
                                {
                                    (this.state.form_response !== undefined)?
                                        <span>{this.state.form_response}</span>
                                    :""
                                }
                            </div>
                        </form>
                    </div>
                
                    <div className={'col-md-6'}>
                        <SubDisplay
                            screen_title={'Childrens Details'}
                            screen_data={this.state.child_list}
                        />
                    </div>
                </div>

            </div>
        )
    }
}
