class Beneficiary extends Component{
    constructor(props){
        super(props);
        this.state = {
            beneficiary_list : [],
            beneficiary : {...beneficiary_details},
            form_response : ""
        };

        this.onSaveBeneficiary =this.onSaveBeneficiary.bind(this);
        this.onChangeHandler  = this.onChangeHandler.bind(this);
        this.loadBeneficiaries = this.loadBeneficiaries.bind(this);
    };

    onSaveBeneficiary(e){
        let self = this;
        let isError = false;
        let errorMessage = '';

        if (Utilities.isEmpty(this.state.beneficiary.full_names)){
            isError = true;
            errorMessage = 'Full Names cannot be Empty';
        }

        if (Utilities.isEmpty(this.state.beneficiary.surname)){
            isError = true;
            errorMessage= 'Surname cannot be empty';
        }

        if (!Utilities.isIDNumber(this.state.beneficiary.id_number)){
            isError = true;
            errorMessage = 'ID Number is not valid';
        }

        if (Utilities.isEmpty(this.state.beneficiary.date_of_birth)){
            isError = true;
            errorMessage = 'Date of Birth cannot be empty';
        }
        if (Utilities.isEmpty(this.state.beneficiary.relationship)){
            isError = true;
            errorMessage = 'Please select a relationship';
        }

        if (!isError){
            Axios.post(policy_routes.save_beneficiary_url,'&data=' + JSON.stringify(this.state.beneficiary)).then(
                function(response){
                    if (response.status === 200){
                        return response.data;
                    }else{
                        throw new Error('Error saving beneficiary');
                    }
                }
            ).then(function(json_data){
                let message = json_data.message;
                self.setState({
                    form_response : message
                });
            }).catch(function(err){
                let message = err.message;
                self.setState({
                    form_response : message
                });
            });
        }else{
            self.setState({
                form_response : errorMessage
            })
        }

    }

    onChangeHandler(e){
        let beneficiary = Object.assign({},this.state.beneficiary);

        switch (e.target.name) {
            case 'full_names' : beneficiary.full_names = e.target.value;break;
            case 'surname' : beneficiary.surname = e.target.value;break;
            case 'id_number' : beneficiary.id_number = e.target.value;break;
            case 'date_of_birth' : beneficiary.date_of_birth = e.target.value;break;
            case 'relationship' : beneficiary.relationship = e.target.value;break;
            default:break;
        }

        this.setState({
            beneficiary: beneficiary
        });
    }

    loadBeneficiaries(e){
        let self = this;
        Axios.get(routes.load_beneficiaries_url).then(function(response){
            if (response.status === 200){
                return response.data;
            }else{
                throw new Error ('Error loading beneficiaries');
            }
        }).then(function(json_data){
            self.setState({
                beneficiary_list : json_data
            });
        }).catch(function(err){
            let message = err.message;
            self.setState({
                form_response : message
            });
        });
    }

    componentWillMount(e){
        this.loadBeneficiaries(e);
    }

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}><strong> <i className={'fa fa-users'}> </i> Beneficiary </strong></h3>
                </div>
                <div className={'row'}>
                    <div className={'col-md-6'}>
                        <form className={'form-horizontal'}>
                            <div className={'form-group'}>
                                <label>Names</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'full_names'} value={this.state.beneficiary.full_names} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>Surname</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'surname'} value={this.state.beneficiary.surname} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>ID Number</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'id_number'} value={this.state.beneficiary.id_number} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>Date Of Birth</label>
                                <div className={'input-group'}>
                                    <input type={'text'} className={'form-control'} name={'date_of_birth'} value={this.state.beneficiary.date_of_birth} onChange={e => this.onChangeHandler(e)} />
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <label>Relationship</label>
                                <div className={'input-group'}>

                                    <select className={'form-control'} name={'relationship'} value={this.state.beneficiary.relationship} onChange={e => this.onChangeHandler(e)} >
                                        <option value={'daughter'}>Daughter</option>
                                        <option value={'son'}>Son</option>
                                    </select>
                                </div>
                            </div>

                            <div className={'form-group'}>
                                <div className={'input-group'}>
                                    <button type={'text'} name={'save_child'} className={'btn btn-success btn-block'} onClick={e => this.onSaveBeneficiary(e)}> <strong> <i className={'fa fa-save'}> </i> Save Beneficiary</strong> </button>
                                </div>
                            </div>

                            <div className={'form-group'}>
                                {
                                    (this.state.form_response !== undefined)?
                                        <span>{this.state.form_response}</span>
                                    :""
                                }
                            </div>
                        </form>
                    </div>
                    <div className={'col-md-6'}>

                        <SubDisplay
                            screen_title={'Beneficiaries'}
                            screen_data={this.state.beneficiary_list}
                        />                                        
                    </div>
                </div>
            </div>
        )
    }
}
class SubDisplay extends Component{
    constructor(props){
        super(props);
        this.state = {
            screen_title : this.props.screen_title,
            screen_display_list : []
        };

        this.Map_screen_display = this.Map_screen_display.bind(this);

    };

    Map_screen_display(e){
        let self = this;
        if (self.props.screen_data !== undefined){
            self.setState({
                screen_display_list: self.props.screen_data
            });
        }
    }

    componentWillMount(e){
        this.Map_screen_display(e);
    }

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <small> <i className={'fa fa-users'}> </i> {(this.state.screen_title) ? this.state.screen_title : "Sub Display"} </small> </strong> </h3>
                </div>

                <table className={'table table-responsive'}>
                    <thead>
                        <tr>
                            <td> <strong> Names </strong></td>
                            <td> <strong> Surname </strong></td>
                            <td> <strong> ID Number </strong></td>
                            <td> <strong> Date of Birth </strong></td>
                            <td> <strong> Relationship </strong></td>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.screen_display_list.map( screen => {
                                return(
                                    <tr>
                                        <td> <strong> <small> <em> {screen.full_names} </em></small></strong></td>
                                        <td> <strong> <small> <em> {screen.surname} </em></small></strong></td>
                                        <td> <strong> <small> <em> {screen.id_number} </em></small></strong></td>
                                        <td> <strong> <small> <em> {screen.date_of_birth} </em></small></strong></td>
                                        <td> <strong> <small> <em> {screen.relationship} </em></small></strong></td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                    <tfoot>
                        <tr>
                            <td> <strong> Names </strong></td>
                            <td> <strong> Surname </strong></td>
                            <td> <strong> ID Number </strong></td>
                            <td> <strong> Date of Birth </strong></td>
                            <td> <strong> Relationship </strong></td>
                        </tr>
                    </tfoot>
                </table>
            </div>
        )
    }
}

class PolicyHolder extends Component{
    constructor(props){
        super(props);
        this.state = {
            policy_holder : {...policy_holder_details},
            postal_address : {...Addresses.postal_address_detail},
            physical_address : {...Addresses.physical_address_details},
            contact_details : {...Contacts.contact_details},
            banking_details : {...banking_details},

            show_personal_details : true,
            show_postal_address : false,
            show_physical_address : false,
            show_contact_details : false,
            show_banking_details : false,


            form_response : "",
        };

        this.onChangeHandler = this.onChangeHandler.bind(this);
        this.onShow = this.onShow.bind(this);
        this.loadPostalAddress = this.loadPostalAddress.bind(this);
        this.loadPhysicalAddress = this.loadPhysicalAddress.bind(this);
        this.loadContactDetails = this.loadContactDetails.bind(this);
        this.loadBankingDetails = this.loadBankingDetails.bind(this);
        this.loadPolicyHolder = this.loadPolicyHolder.bind(this);
        this.onSaveHandler = this.onSaveHandler.bind(this);

    };

    onSaveHandler(e){
        let isError = false;
        let errorMessage = '';
        let self = this;

        if (Utilities.isEmpty(this.state.policy_holder.title)){
            isError = true;
            errorMessage = 'Please select Title';
        }

        if (Utilities.isEmpty(this.state.policy_holder.full_names)){
            isError = true;
            errorMessage = 'Full Names cannot be empty';
        }

        if (Utilities.isEmpty(this.state.policy_holder.surname)){
            isError = true;
            errorMessage = 'Surname cannot be empty';
        }

        if (!Utilities.isIDNumber(this.state.policy_holder.id_number)){
            isError = true;
            errorMessage = "ID Number not valid";
        }

        if (Utilities.isEmpty(this.state.policy_holder.date_of_birth)){
            isError = true;
            errorMessage = 'Date of Birth cannot be empty';
        }

        if (!isError){
            Axios.post(policy_routes.save_policy_holder_url,'&data=' + JSON.stringify(this.state.policy_holder)).then(
                function(response){
                    if (response.status === 200){
                        return response.data
                    }else{
                        throw new Error('Error saving Policy Holder');
                    }
                }
            ).then(function(json_data){
                let message = json_data.message;
                self.setState({
                    form_response : message
                });
            }).catch(function(err){
                let message = err.message;
                self.setState({
                    form_response : message
                })
            });
        }else{
            self.setState({
                form_response : errorMessage
            });
        }

    }

    onChangeHandler(e){
        let policy_holider = Object.assign({},this.state.policy_holder);
        switch (e.target.name) {
            case 'title' : policy_holider.title = e.target.value;break;
            case 'surname' : policy_holider.surname = e.target.value;break;
            case 'full_names' : policy_holider.full_names = e.target.value;break;
            case 'id_number' : policy_holider.id_number = e.target.value;break;
            case 'date_of_birth' : policy_holider.date_of_birth = e.target.value;break;
            case 'nationality' : policy_holider.nationality = e.target.value;break;

            default: break;
        };

        this.setState({
            policy_holder: policy_holider
        });

    };

    onShow(e){        
    switch (e.target.name) {
        case 'personal-details' :
            this.setState({
                show_personal_details : true,
                show_postal_address : false,
                show_physical_address : false,
                show_contact_details : false,
                show_banking_details : false

            });break;

        case 'physical-address':
            this.setState({
                show_personal_details : false,
                show_postal_address : false,
                show_physical_address : true,
                show_contact_details : false,
                show_banking_details : false

            });break;

        case 'postal-address':
            this.setState({
                show_personal_details : false,
                show_postal_address : true,
                show_physical_address : false,
                show_contact_details : false,
                show_banking_details : false

            });break;
        case 'contact-details':
            this.setState({
                show_personal_details : false,
                show_postal_address : false,
                show_physical_address : false,
                show_contact_details : true,
                show_banking_details : false
            });break;

        case 'banking-details':
            this.setState({
                show_personal_details : false,
                show_postal_address : false,
                show_physical_address : false,
                show_contact_details : false,
                show_banking_details : true            

            });break;

        default: break;


        }
    };

    loadPostalAddress(e){
        let self = this;

        if (self.props.policy_number){

        Axios.get(address_routes.load_postal_address_url + self.props.policy_number).then(function (response) {
            if (response.status === 200) {
                return response.data
            } else {
                throw new Error("Error loading postal address");
            }
        }).then(function (data) {
            self.setState({
                postal_address: data
            });
        }).catch(function (err) {
            console.log(err.message)
        })
    }else{
            console.log("Error initializing policy holder missing policy number");
    }

    };
    loadPhysicalAddress(e) {
        let self = this;
        if (self.props.policy_number !== undefined){
        Axios.get(address_routes.load_physical_address_url + self.props.policy_number).then(function (response) {
            if (response.status === 200) {
                return response.data
            } else {
                throw new Error("Error loading physical address");
            }
        }).then(function (data) {
            self.setState({
                physical_address: data
            })
        }).catch(function (err) {
            console.log(err.message);
        })
    }else{
        console.log('Error initializing policy holder missing policy number');
    }};
    loadContactDetails(e){
        let self = this;
        if (self.props.policy_number !== undefined){
        Axios.get(contacts_routes.load_contact_details_url + self.props.policy_number).then(function (response) {

            if (response.status === 200) {
                return response.data
            } else {
                throw new Error("Error loading contact details");
            }
        }).then(function (data) {
            self.setState({
                contact_details: data
            })
        }).catch(function (err) {
            console.log(err.message)
        })
    }else{
        console.log('Error initializing policy holder missing policy number');
    }};


    loadBankingDetails(e){
        let self = this;
        if (self.props.policy_number !== undefined){
        Axios.get(banking_routes.load_banking_details_url + self.props.policy_number).then(function (response) {
            if (response.status === 200) {
                return response.data
            } else {
                throw new Error("Error fetching account details")
            }
        }).then(function (json_data) {
            let banking_details = Object.assign({}, self.state.banking_details);
            banking_details = json_data;
            self.setState({
                banking_details: banking_details
            });
        }).catch(function (err) {
            let message = err.message;
            self.setState({
                form_response: message
            })
        });
    }else{
            console.log('Error initializing policy holder missing policy number');
    }};


    loadPolicyHolder(e){
        let self = this;
        if (self.props.policy_number !== undefined){
            Axios.get(policy_routes.load_policy_holder_url + self.props.policy_number).then(function(response){
                if (response.status === 200){
                    return response.data;
                }else{
                    throw new Error('Error loading personal details');
                }
            }).then(function(json_data){
                    let policy_holder = Object.assign({},this.state.policy_holider);
                    policy_holder = json_data
                    self.setState({
                        policy_holder:policy_holder
                    });
            }).catch(function(err){
                let message = err.message;
                self.setState({
                    form_response : message
                });
            });
        }else{
            console.log('Error initializing policy holder missing policy number');
        }

    };
    

    componentWillMount(e){

        this.loadPostalAddress(e);
        this.loadPhysicalAddress(e);
        this.loadContactDetails(e);
        this.loadBankingDetails(e);
        this.loadPolicyHolder(e);
        
    };


    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <i className={'fa fa-user'}> </i> Policy Holder </strong></h3>

                    <div className={'row pull-right'}>
                        <button className='btn btn-box-tool btn-outline-dark' name={'personal-details'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-user'> </i> Personal Details </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'physical-address'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-map-o'> </i> Physical Address </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'postal-address'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-envelope'> </i> Postal Address </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'contact-details'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-mobile'> </i> Contact Details </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'banking-details'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-bank'> </i> Banking Details </strong></button>
                    </div>
                </div>

                <div className={'row'}>
                    <div className={'col-md-6'}>
                        <div className={'box box-footer'}>
                            <div className={'box box-header'}>
                                <h3 className={'box-title'}> <strong> <i className={'fa fa-user'}> </i> Personal Details </strong></h3>
                            </div>
                            <form className={'form-horizontal'}>

                                <div className={'form-group'}>
                                    <label>Title</label>
                                    <div className={'input-group'}>
                                        <select className={'form-control'} name={'title'} value={this.state.policy_holder.title} onChange={e => this.onChangeHandler(e)} >
                                            <option value={'mr'}> MR </option>
                                            <option value={'mrs'}> Mrs </option>
                                            <option value={'miss'}> Miss </option>
                                        </select>
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>Surname</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'surname'} value={this.state.policy_holder.surname} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>Full Names</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'full_names'} value={this.state.policy_holder.full_names} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>ID Number</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'id_number'} value={this.state.policy_holder.id_number} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>DOB</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'date_of_birth'} value={this.state.policy_holder.date_of_birth} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>Nationality</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'nationality'} value={this.state.policy_holder.nationality} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <button type={'button'} className={'btn btn-success btn-block'} onClick={e => this.onSaveHandler(e)}><strong> <i className={'fa fa-save'}> </i> Save Policy </strong></button>
                                </div>

                                <div className={'form-group'}>
                                    {
                                        (this.state.form_response) ?
                                            <span>{this.state.form_response}</span>
                                        :""
                                    }
                                </div>
                            </form>

                        </div>
                    </div>
                    <div className={'col-md-6'}>
                        {
                            (this.state.show_postal_address) ?
                                <Addresses.Postal 
                                    postal_id={this.state.policy_holder.policy_number}
                                    postal_address = {this.state.postal_address}
                                />
                            :""
                        }
                        {
                            (this.state.show_physical_address)?
                                <Addresses.Physical 
                                    physical_id={this.state.policy_holder.policy_number}
                                    physical_address={this.state.physical_address}
                                />
                            :""
                        }
                        {
                            (this.state.show_contact_details) ?
                                <Contacts.ContactDetails 
                                    contact_id={this.state.policy_holder.policy_number}
                                    contact_details={this.state.contact_details}
                                />
                            :""
                        }
                        {
                            (this.state.show_banking_details) ?
                                <BankingDetails 
                                    banking_id={this.state.policy_holder.policy_number}
                                    banking_details={this.state.banking_details}
                                />
                            :""
                        }
                    </div>
                </div>

            </div>
        )
    }
}


