import React, { Component } from "react";

import "./Admin.css";
import Intro from "../Intro/Intro";
import User from "../User/User";
import Axios from "axios";
import * as app_constants from '../../constants/program_constants';
import Covers from '../Funeral/Covers/Covers';

import SignOutButton from "../User/SignOut/SignOut";

import {
    firebase, auth
} from '../../firebase'; 
import SignInPage from '../User/SignIn/SignIn';
import Leads from "../Funeral/Leads/Leads";
import Messaging from "../Messaging/Messaging";
import ClientTrace from "../ClientTrace/ClientTrace";
import FleetManagement from "../FleetManagement/FleetManagement";
import Collections from "../Funeral/Collections/Collections";
class Admin extends Component{
    constructor(){
        super();
        this.state = {
            isUserLoggedIn : true,
            show_covers : true,
            show_collections : false,
            show_leads : false,
            show_messaging : false,
            show_client_trace : false,
            show_fleet_management : false
        };

        this.onShow = this.onShow.bind(this);
    }

    onShow(e){

        switch(e.target.name){
            case 'covers' : 
                this.setState({
                    show_covers: true,
                    show_collections : false,
                    show_leads: false,
                    show_messaging: false,
                    show_client_trace: false,
                    show_fleet_management: false
                });break;
            case 'leads' :
                this.setState({
                    show_covers: false,
                    show_collections: false,
                    show_leads: true,
                    show_messaging: false,
                    show_client_trace: false,
                    show_fleet_management: false
                });break;

            case 'messaging':
                this.setState({
                    show_covers: false,
                    show_collections: false,
                    show_leads: false,
                    show_messaging: true,
                    show_client_trace: false,
                    show_fleet_management: false

                });break;

            case 'client-trace':
                this.setState({
                    show_covers: false,
                    show_collections: false,
                    show_leads: false,
                    show_messaging: false,
                    show_client_trace: true,
                    show_fleet_management: false

                });break;

            case 'fleet-management':
                this.setState({
                    show_covers: false,
                    show_collections: false,
                    show_leads: false,
                    show_messaging: false,
                    show_client_trace: false,
                    show_fleet_management: true
                });break;

            case 'collections':
                this.setState({
                    show_covers: false,
                    show_collections: true,
                    show_leads: false,
                    show_messaging: false,
                    show_client_trace: false,
                    show_fleet_management: false
                }); break;
            default:break;
        }
        

    }
    render(){

        let isUserLoggedIn = ((firebase.auth.currentUser !== "") && (firebase.auth.currentUser  !== null ));

        if (isUserLoggedIn){
                return(
                        <div className="admin">
                            <div className="box box-body">
                                <Intro heading={app_constants.app_name} slogan={app_constants.app_descrition} />
                                <div className="box box-header">
                                    <h3 className="box-title">  <i className="fa fa-dashboard"> </i> Admin </h3>
                                    <button type="button" className="btn btn-warning pull-right" name="signout" onClick={e => this.onShow(e)}><strong> <em> <i className="fa fa-sign-out"> </i> SignOut</em></strong></button>
                                </div>
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="box box-primary">

                                            <button className={'btn btn-app btn-outline-success btn-block'} name={'covers'} onClick={e  => this.onShow(e)}><strong> <i className={'fa fa-users'}> </i> Covers</strong></button>
                                            <button className={'btn btn-app btn-outline-success btn-block'} name={'leads'} onClick={e => this.onShow(e)}><strong> <i className={'fa fa-bullhorn'}> </i> Leads</strong></button>
                                            <button className={'btn btn-app btn-outline-success btn-block'} name={'collections'} onClick={e => this.onShow(e)}><strong> <i className={'fa fa-money'}> </i> Collections</strong></button>
                                            <button className={'btn btn-app btn-outline-success btn-block'} name={'messaging'} onClick={e => this.onShow(e)}><strong> <i className={'fa fa-envelope'}> </i> Messaging</strong></button>
                                            <button className={'btn btn-app btn-outline-success btn-block'} name={'client-trace'} onClick={e => this.onShow(e)}><strong> <i className={'fa fa-user-plus'}> </i> Client Trace</strong></button>
                                            <button className={'btn btn-app btn-outline-success btn-block'} name={'fleet-management'} onClick={e => this.onShow(e)}><strong> <i className={'fa fa-bus'}> </i> Fleet Management</strong></button>

                                        </div>
                                    
                                    </div>
                                    <div className="col-md-9">
                                    {
                                        (this.state.show_covers) ?
                                            <Covers />
                                        :""
                                    }
                                    {
                                        (this.state.show_collections)?
                                            <Collections />
                                        :''
                                    }
                                    {
                                        (this.state.show_leads) ?
                                            <Leads />
                                        :''
                                    }
                                    {
                                        (this.state.show_messaging)?
                                            <Messaging />
                                        :''
                                    }

                                    {
                                        (this.state.show_client_trace)?
                                            <ClientTrace />
                                        :""
                                    }

                                    {
                                        (this.state.show_fleet_management)?
                                            <FleetManagement />
                                        :''
                                    }
                                        
                                    </div>
                                </div>


                            </div>
                        </div>
                    )
            }
            else{
            return (<SignInPage message={"Please sign in before using the administration module"} />)
            }
    }
}



export default Admin;