import React, { Component } from "react";
import * as routes from '../../constants/routes';
import Axios from 'axios';


let trace_details = {
    id_number : '',
    names : '',
    surname : '',
    cell : ''
};

let trace_balances_details = {
    available_balance : '',
    possible_id_verifications : '',
    possible_drivers_license_verifications : '',
    possible_bank_account_verifications : '',
    possible_consumer_traces : '',
    possible_cell_to_id : '',
    possible_find_addresses : ''
}

let trace_settings_details ={
    settings_id : '',
    enable_consumer_trace : false,
    autorun_consumer_trace : false,
    enable_cell_to_id : false,
    autorun_cell_to_id : false,
    enable_find_addresses : false,
    autorun_find_addresses : false
}

let verification_settings_details = {
    settings_id : ''


}



class TraceBalances extends Component{
    constructor(props){
        super(props);
        this.state = {
            trace_balances : trace_balances_details,
        };
    };

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <i className={'fa fa-bank'}> </i> Trace &amp; Verifications Balances </strong></h3>
                    <div className={'row pull-right'}>                        
                        <button type={'button'} className='btn btn-box-tool btn-outline-danger' onClick={e => {
                            return (
                                this.props.onClose(e)
                            )
                        }}><strong><i className='fa fa-close'> </i> Close </strong></button>
                    </div>
                    


                </div>

                <ul className={'list-group'}>
                    <li className='list-group-heading'><span><i className={'fa fa-money'}> </i> Available Balance : R {this.state.trace_balances.available_balance} </span></li>    
                    <li className="list-group-item"><i className="fa fa-user"> </i> <strong> ID Number Verifications : {this.state.trace_balances.possible_id_verifications} </strong></li>
                    <li className="list-group-item"><i className="fa fa-road"> </i>  <strong> Driver License Verifications : {this.state.trace_balances.possible_drivers_license_verifications}</strong></li>
                    <li className="list-group-item"><i className="fa fa-credit-card"> </i> <strong> Bank Account Verifications : {this.state.trace_balances.possible_bank_account_verifications}</strong></li>
                    <li className="list-group-item"><i className="fa fa-shopping-basket"> </i> <strong> Consumer Trace : {this.state.trace_balances.possible_consumer_traces}</strong></li>
                    <li className="list-group-item"><i className="fa fa-mobile-phone"> </i> <strong>Cell to ID Number : {this.state.trace_balances.possible_cell_to_id}</strong></li>
                    <li className="list-group-item"><i className="fa fa-envelope"> </i> <strong> Find Addresses : {this.state.trace_balances.possible_find_addresses}</strong></li>
                    <li className="list-group-item"><button className={'btn btn-app btn-block btn-outline-success'}><strong> <i className={'fa fa-cc-amex'}> </i> Make Deposit </strong></button></li>
                </ul>

                <div className={'form-group'}>
                    
                </div>
            </div>
        )
    }
}


