class ShowTraces extends Component{
    constructor(props){
        super(props);
        this.state ={
            traces : '',
            show_consumer_trace : false,
            show_cell_to_id : false,
            show_addresses_trace : false,

            form_response : ''
        };
        this.onShow = this.onShow.bind(this);
    };

    onShow(e){
        switch(e.target.name){
            case 'consumer-trace':
                this.setState({
                    show_consumer_trace: true,
                    show_cell_to_id: false,
                    show_addresses_trace: false,
                });break;
            case 'cell-to-id':
                this.setState({
                    show_consumer_trace: false,
                    show_cell_to_id: true,
                    show_addresses_trace: false,
                }); break;
            case 'addresses':
                this.setState({
                    show_consumer_trace: false,
                    show_cell_to_id: false,
                    show_addresses_trace: true,
                }); break;

            default:break;
        }
    }

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}>Traces</h3>
                </div>
                <div className={'row'}>
                    <div className={'col-md-3'}>
                        <div className='box box-footer'>
                            <button className='btn btn-app btn-outline-light btn-block' name={'consumer-trace'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-user-md'> </i> Consumer Trace</strong></button>
                            <button className='btn btn-app btn-outline-light btn-block' name={'cell-to-id'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-user-md'> </i> Cell to ID Number</strong></button>
                            <button className='btn btn-app btn-outline-light btn-block' name={'addresses'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-user-md'> </i> Addresses</strong></button>
                        </div>
                    </div>
                    <div className={'col-md-9'}>

                        {
                            (this.state.show_consumer_trace) ?
                                <ConsumerTrace />
                            :''
                        }

                        {
                            (this.state.show_cell_to_id) ?
                                <CellToIDTrace />
                            :''
                        }

                        {
                            (this.state.show_addresses_trace)?
                                <AddressesTrace />
                            :''
                        }
                    </div>
                </div>


            </div>
        )
    }
}


class RunTrace extends Component{
    constructor(props){
        super(props);
        this.state = {
            trace : '',
            show_verifications: false,
            show_traces: false,
            form_response : ''
        };

        this.onShow = this.onShow.bind(this);
    };

    onShow(e){
    
        switch(e.target.name){
            case 'verifications' : 
                this.setState({
                    show_verifications: true,
                    show_traces: false
                });break;
            case 'traces':
                this.setState({
                    show_verifications: false,
                    show_traces: true
                }); break;

            default:break;
        }
    }


    render(){
        return(
            <div className='box box-footer'>
                <div className='box box-header'>
                    <h3 className='box-title'><strong> <i className='fa fa-search'> </i> Run Trace</strong></h3>

                    <div className='row pull-right'>
                        <button type={'button'} className='btn btn-box-tool btn-outline-dark' name={'verifications'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-search'> </i> Verifications </strong></button>
                        <button type={'button'} className='btn btn-box-tool btn-outline-dark' name={'traces'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-search'> </i> Traces </strong></button>
                        
                        |
                        <button type={'button'} className='btn btn-box-tool btn-outline-danger' onClick={e => {
                            return (
                                this.props.onClose(e)
                            )
                        }}><strong><i className='fa fa-close'> </i> Close </strong></button>

                    </div>

                

                            
                </div>
            
                        {
                            (this.state.show_verifications) ?
                                <ShowVerifications />
                            :""
                        }       

                        {(this.state.show_traces)?
                            <ShowTraces />
                        :''
                        }                         
            </div>
        )
    }
}

class ClientTrace extends Component{
    constructor(){
        super();
        this.state = {
            client : '',
            show_run_trace : false,
            show_trace_settings : false,
            form_response : ''
        };
        this.onShow = this.onShow.bind(this);
        this.onCloseHandler = this.onCloseHandler.bind(this);
    };

    onCloseHandler(e){
        this.setState({
            show_run_trace: false,
            show_trace_settings: false,
        });
    }


    onShow(e){
        switch(e.target.name){
            case 'run-trace' :
                this.setState({
                    show_run_trace: true,
                    show_trace_settings: false
                });break;
            case 'trace-settings' :
                this.setState({
                    show_run_trace: false,
                    show_trace_settings: true
                }); break;
            default:break;
        }
    }

    render(){
        return(
            <div className='box box-footer'>
                <div className='box box-header'>
                    <h3 className='box-title'> <strong> <i className='fa fa-search-plus'> </i> Client Trace</strong></h3>

                    <div className='row pull-right'>
                        <button className='btn btn-outline-dark btn-box-tool' name={'run-trace'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-search-minus'> </i> Start Trace </strong></button>                        
                        <button className='btn btn-outline-dark btn-box-tool' name={'trace-settings'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-server'> </i> Settings </strong></button>                        
                    </div>
                </div>
                {
                    !(this.state.show_run_trace || this.state.show_trace_settings)?
                        <div className={'box box-footer'}>
                            <div className={'box box-header'}>
                                <h3 className={'box-title'}><strong> Traces Module </strong></h3>
                            </div>
                            <blockquote>
                                <span>Traces and Verification module allows you to trace and verify your clients personally 
                                identifiable information all in one easy to use module.</span>
                            </blockquote>

                            <blockquote>

                                <span>This module can save you money as you can always verify client information especially
                                when in doubt, before you actually make use of it thereby allowing you to also find the 
                                correct information incase of a mistake.</span>


                            </blockquote>

                        </div>
                    :''
                }


                {
                    (this.state.show_run_trace)?
                        <RunTrace 
                            onClose={this.onCloseHandler}
                        />
                    :""
                }
                {
                    (this.state.show_trace_settings) ?
                        <TVSettings
                         onClose={this.onCloseHandler}
                        />
                    :""
                }
            </div>
        )
    }
}

export default ClientTrace;